	module.exports = {"data":{"postsJson":{"title":"First Missionary Journey","questionsTitle":"FOLLOWING GODLY EXAMPLES","display":"block","bookName":"Acts","ref":"13:1-14:28","html":"<p>   <strong>13</strong><u>1</u>Now in the assembly that was at Antioch there were some prophets and teachers: Barnabas, Simeon who was called Niger, Lucius of Cyrene, Manaen the foster brother of Herod the tetrarch, and Saul. <u>2</u>As they served the Lord and fasted, the Holy Spirit said,</p><h1>GOD</h1><h2>Separate Barnabas and Saul for me, for the work to which I have called them.</h2><p>   <u>3</u>Then, when they had fasted and prayed and laid their hands on them, they sent them away. <u>4</u>So, being sent out by the Holy Spirit, they went down to Seleucia. From there they sailed to Cyprus. <u>5</u>When they were at Salamis, they proclaimed the word of God in the Jewish synagogues. They had also John as their attendant. <u>6</u>When they had gone through the island to Paphos, they found a certain sorcerer, a false prophet, a Jew, whose name was Bar Jesus, <u>7</u>who was with the proconsul, Sergius Paulus, a man of understanding. This man summoned Barnabas and Saul, and sought to hear the word of God. <u>8</u>But Elymas the sorcerer (for so is his name by interpretation) withstood them, seeking to turn aside the proconsul from the faith. <u>9</u>But Saul, who is also called Paul, filled with the Holy Spirit, fastened his eyes on him, <u>10</u>and said,</p><h1>PAUL</h1><h3>Full of all deceit and all cunning, you son of the devil, you enemy of all righteousness, will you not cease to pervert the right ways of the Lord? <u>11</u>Now, behold, the hand of the Lord is on you, and you will be blind, not seeing the sun for a season!</h3><p>   Immediately a mist and darkness fell on him. He went around seeking someone to lead him by the hand. <u>12</u>Then the proconsul, when he saw what was done, believed, being astonished at the teaching of the Lord.<br />   <u>13</u>Now Paul and his company set sail from Paphos, and came to Perga in Pamphylia. John departed from them and returned to Jerusalem. <u>14</u>But they, passing on from Perga, came to Antioch of Pisidia. They went into the synagogue on the Sabbath day, and sat down. <u>15</u>After the reading of the law and the prophets, the rulers of the synagogue sent to them, saying,</p><h1>SYNAGOGUE LEADERS</h1><h4>Brothers, if you have any word of exhortation for the people, speak.</h4><p>   <u>16</u>Paul stood up, and beckoning with his hand said,</p><h1>PAUL</h1><h3>Men of Israel, and you who fear God, listen. <u>17</u>The God of this people* chose our fathers, and exalted the people when they stayed as aliens in the land of Egypt, and with an uplifted arm, he led them out of it. <u>18</u>For a period of about forty years he put up with them in the wilderness. <u>19</u>When he had destroyed seven nations in the land of Canaan, he gave them their land for an inheritance, for about four hundred fifty years. <u>20</u>After these things he gave them judges until Samuel the prophet. <u>21</u>Afterward they asked for a king, and God gave to them Saul the son of Kish, a man of the tribe of Benjamin, for forty years. <u>22</u>When he had removed him, he raised up David to be their king, to whom he also testified, ‘I have found David the son of Jesse, a man after my heart, who will do all my will.’ <u>23</u>From this man’s offspring,* God has brought salvation* to Israel according to his promise, <u>24</u>before his coming, when John had first preached the baptism of repentance to Israel.* <u>25</u>As John was fulfilling his course, he said, ‘What do you suppose that I am? I am not he. But behold, one comes after me the sandals of whose feet I am not worthy to untie.’ <u>26</u>Brothers, children of the stock of Abraham, and those among you who fear God, the word of this salvation is sent out to you. <u>27</u>For those who dwell in Jerusalem, and their rulers, because they didn’t know him, nor the voices of the prophets which are read every Sabbath, fulfilled them by condemning him. <u>28</u>Though they found no cause for death, they still asked Pilate to have him killed. <u>29</u>When they had fulfilled all things that were written about him, they took him down from the tree, and laid him in a tomb. <u>30</u>But God raised him from the dead, <u>31</u>and he was seen for many days by those who came up with him from Galilee to Jerusalem, who are his witnesses to the people. <u>32</u>We bring you good news of the promise made to the fathers, <u>33</u>that God has fulfilled the same to us, their children, in that he raised up Jesus. As it is also written in the second psalm,<br />   ‘You are my Son.<br />   Today I have become your father.’*<br />   <u>34</u>Concerning that he raised him up from the dead, now no more to return to corruption, he has spoken thus: ‘I will give you the holy and sure blessings of David.’* <u>35</u>Therefore he says also in another psalm, ‘You will not allow your Holy One to see decay.’* <u>36</u>For David, after he had in his own generation served the counsel of God, fell asleep, and was laid with his fathers, and saw decay. <u>37</u>But he whom God raised up saw no decay. <u>38</u>Be it known to you therefore, brothers,* that through this man is proclaimed to you remission of sins, <u>39</u>and by him everyone who believes is justified from all things, from which you could not be justified by the law of Moses. <u>40</u>Beware therefore, lest that come on you which is spoken in the prophets:<br />   <u>41</u>‘Behold, you scoffers, and wonder, and perish;<br />   for I work a work in your days,<br />   a work which you will in no way believe, if one declares it to you.’*</h3><p>   <u>42</u>So when the Jews went out of the synagogue, the Gentiles begged that these words might be preached to them the next Sabbath. <u>43</u>Now when the synagogue broke up, many of the Jews and of the devout proselytes followed Paul and Barnabas; who, speaking to them, urged them to continue in the grace of God. <u>44</u>The next Sabbath almost the whole city was gathered together to hear the word of God. <u>45</u>But when the Jews saw the multitudes, they were filled with jealousy, and contradicted the things which were spoken by Paul, and blasphemed.<br />   <u>46</u>Paul and Barnabas spoke out boldly, and said,</p><h1>PAUL & BARNABAS</h1><h3>It was necessary that God’s word should be spoken to you first. Since indeed you thrust it from you, and judge yourselves unworthy of eternal life, behold, we turn to the Gentiles. <u>47</u>For so has the Lord commanded us, saying,<br />   ‘I have set you as a light for the Gentiles,<br />   that you should bring salvation to the uttermost parts of the earth.’*</h3><p>   <u>48</u>As the Gentiles heard this, they were glad, and glorified the word of God. As many as were appointed to eternal life believed. <u>49</u>The Lord’s word was spread abroad throughout all the region. <u>50</u>But the Jews stirred up the devout and prominent women and the chief men of the city, and stirred up a persecution against Paul and Barnabas, and threw them out of their borders. <u>51</u>But they shook off the dust of their feet against them, and came to Iconium. <u>52</u>The disciples were filled with joy with the Holy Spirit.<br />   <strong>14</strong><u>1</u>In Iconium, they entered together into the synagogue of the Jews, and so spoke that a great multitude both of Jews and of Greeks believed. <u>2</u>But the disbelieving* Jews stirred up and embittered the souls of the Gentiles against the brothers. <u>3</u>Therefore they stayed there a long time, speaking boldly in the Lord, who testified to the word of his grace, granting signs and wonders to be done by their hands. <u>4</u>But the multitude of the city was divided. Part sided with the Jews, and part with the apostles. <u>5</u>When some of both the Gentiles and the Jews, with their rulers, made a violent attempt to mistreat and stone them, <u>6</u>they became aware of it, and fled to the cities of Lycaonia, Lystra, Derbe, and the surrounding region. <u>7</u>There they preached the Good News.<br />   <u>8</u>At Lystra a certain man sat, impotent in his feet, a cripple from his mother’s womb, who never had walked. <u>9</u>He was listening to Paul speaking, who, fastening eyes on him, and seeing that he had faith to be made whole, <u>10</u>said with a loud voice,</p><h1>PAUL</h1><h3>Stand upright on your feet!</h3><p>He leaped up and walked. <u>11</u>When the multitude saw what Paul had done, they lifted up their voice, saying in the language of Lycaonia,</p><h1>THE PEOPLE OF LYSTRA</h1><h4>The gods have come down to us in the likeness of men!</h4><p><u>12</u>They called Barnabas “Jupiter”, and Paul “Mercury”, because he was the chief speaker. <u>13</u>The priest of Jupiter, whose temple was in front of their city, brought oxen and garlands to the gates, and would have made a sacrifice along with the multitudes. <u>14</u>But when the apostles, Barnabas and Paul, heard of it, they tore their clothes, and sprang into the multitude, crying out,</p><h1>PAUL &amp; BARNABAS</h1><h3><u>15</u>Men, why are you doing these things? We also are men of like passions with you, and bring you good news, that you should turn from these vain things to the living God, who made the sky and the earth and the sea, and all that is in them; <u>16</u>who in the generations gone by allowed all the nations to walk in their own ways. <u>17</u>Yet he didn’t leave himself without witness, in that he did good and gave you* rains from the sky and fruitful seasons, filling our hearts with food and gladness.</h3><p>   <u>18</u>Even saying these things, they hardly stopped the multitudes from making a sacrifice to them. <u>19</u>But some Jews from Antioch and Iconium came there, and having persuaded the multitudes, they stoned Paul, and dragged him out of the city, supposing that he was dead.<br />   <u>20</u>But as the disciples stood around him, he rose up, and entered into the city. On the next day he went out with Barnabas to Derbe. <u>21</u>When they had preached the Good News to that city, and had made many disciples, they returned to Lystra, Iconium, and Antioch, <u>22</u>confirming the souls of the disciples, exhorting them to continue in the faith, and that through many afflictions we must enter into God’s Kingdom. <u>23</u>When they had appointed elders for them in every assembly, and had prayed with fasting, they commended them to the Lord, on whom they had believed.<br />   <u>24</u>They passed through Pisidia, and came to Pamphylia. <u>25</u>When they had spoken the word in Perga, they went down to Attalia. <u>26</u>From there they sailed to Antioch, from where they had been committed to the grace of God for the work which they had fulfilled. <u>27</u>When they had arrived, and had gathered the assembly together, they reported all the things that God had done with them, and that he had opened a door of faith to the nations. <u>28</u>They stayed there with the disciples for a long time.</p><h5>*Acts 13:17 TR, NU add “Israel”<br />*Acts 13:23 or, seed<br />*Acts 13:23 TR, NU read “a Savior, Jesus” instead of “salvation”<br />*Acts 13:24 TR, NU read “to all the people of Israel” instead of “to Israel”<br />*Acts 13:33 Psalm 2:7<br />*Acts 13:34 Isaiah 55:3<br />*Acts 13:35 Psalm 16:10<br />*Acts 13:38 The word for “brothers” here and where the context allows may also be correctly translated “brothers and sisters” or “siblings.”<br />*Acts 13:41 Habakkuk 1:5<br />*Acts 13:47 Isaiah 49:6<br />*Acts 14:2 or, disobedient<br />*Acts 14:17 TR reads “us” instead of “you”</h5>","path":"/357","Question1":"Does this portion of Scripture address a character quality you should seek to cultivate? What is it?","Question2":"What actions can you embark on in your life to do this?","Question3":"Does this portion of Scripture address a character flaw you should seek to uproot? What is it?","Question4":"What actions can you embark on your life to do this?","prevPath":"/356","nextPath":"/358"}},"pathContext":{}}

